@mixin flex-centered {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin font($weight, $size, $line-height) {
  font-weight: $weight;
  font-size: $size;
  line-height: $line-height;
}

@mixin flex-wide {
  display: flex;
  justify-content: space-between;
}

@mixin star {
  width: 15px;
  height: 15px;
  margin-right: 4px;
}
